@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.museum {

  margin-bottom: 90px;
  background-color: $colorBackground;
  color: $colorWhite;

  @include onTablet {
    margin-bottom: 105px;
  }

  @include onDesctop {
    margin-bottom: 110px;
  }

  &__wrapper {
    position: relative;
    max-width: 1280px;
    margin: 0 auto;
  }

  &__content {
    box-sizing: border-box;
    padding-top: 80px;
    height: 100vh;
    min-height: 620px;
    overflow: hidden;

    @media(min-height: 700px) {
      padding-top: 130px;
    }

    @include onTablet {
      padding-top: 176px;
      min-height: 900px;
    }

    @include onDesctop {
      padding-top: 180px;
      min-height: 750px;
    }
  }

  &__info {
    margin-bottom: 40px;
  }

  &__suptitle {
    margin: 0 0 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
  }

  &__title {
    margin: 0 0 30px;
    font-family: $titleFont;
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;

    @include onTablet {
      margin: 0 0 20px;
      font-size: 72px;
    }

    @include onDesctop {
      margin: 0 0 30px;
      font-size: 96px;
      max-width: 570px;
    }
  }

  &__text {
    margin: 0 0 40px;

    @include onTablet {
      margin: 0 0 50px;
    }

    @include onDesctop {
      margin: 0 0 81px;
    }
  }

  &__button {
    @extend %button;

    @include onTablet {
      width: 48%;
    }

    @include onDesctop {
      width: 270px;
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 0 18px;
    height: 220px;

    @media(min-height: 670px) {
      height: 273px;
    }

    @media(min-height: 830px) {
      height: 320px;
    }

    @media(min-height: 1000px) {
      height: 360px;
    }

    @include onTablet {
      margin: 0 auto;
      height: 494px;
    }

    @include onDesctop {
      left: 49%;
      height: 663px;
      width: 625px;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }

  &__dates {
    display: none;

    @include onDesctop {
      display: inline-block;
      font-weight: 700;
      font-size: 14px;
      line-height: 125%;

      position: absolute;
      bottom: 0;
      margin-bottom: 50px;

      &::after {
        content: "";
        display: inline-block;
        margin-left: 20px;
        height: 1px;
        width: 469px;
        background-color: $colorWhite;

        position: relative;
        transform: translateY(-4px);
      }
    }
  }
}
