@import "../utils/mixins";
@import "../utils/vars";
@import "../utils/extends";

.menu {
  background-color: $colorBackground;
  color: $colorWhite;

  &__container {
    position: relative;
  }

  &__content {
    height: 100vh;
    box-sizing: border-box;
    overflow: hidden;

    @include onTablet {
      min-height: 950px;
    }

    @include onDesctop {
      min-height: 750px;
    }
  }

  &__nav {
    @extend %headerNav;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 30px;
    }

    @include onDesctop {
      margin-bottom: 20px;
    }
  }

  &__info-items {

    @include onTablet {
      margin-bottom: 50px;
      display: flex;
      column-gap: 123px;
      height: 44px;

      @include onDesctop {
        margin-bottom: 70px;
        column-gap: 83px;
      }
    }
  }

  &__info-item {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 40px;
    }

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__description {
    display: block;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    text-decoration: none;
    color: inherit;

    &--link {
      border-bottom: 1px solid transparent;
      transition: border-bottom 0.3s;
      &:hover {
        border-bottom: 1px solid $colorWhite;
      }
    }
  }

  &__list {
    margin: 0 0 40px;
    padding: 0 0 40px;
    list-style: none;
    border-bottom: 1px solid $colorWhite;

    @include onTablet {
      margin: 0 0 50px;
      padding: 0 0 50px;
    }

    @include onDesctop {
      max-width: 570px;
      margin: 0 0 60px;
      padding: 0 0 60px;
    }
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onTablet {
      margin-bottom: 30px;
    }

    @include onDesctop {
      margin-bottom: 60px;
    }
  }

  &__link {
    font-family: $titleFont;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s;

    &:hover {
      border-bottom: 1px solid $colorWhite;
    }

    @include onTablet {
      font-size: 48px;
      line-height: 120%;
    }
  }

  &__button {
    @extend %button;

    @include onTablet {
      width: 48%;
    }

    @include onDesctop {
      width: 270px;
    }
  }

  &__image {
    display: none;

    @include onTablet {
      display: block;
      height: 466px;
      width: 422px;

      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);

      @media(max-height: 1075px) {
        height: 400px;
        width: 350px;
      }

      @media(max-height: 1000px) {
        height: 340px;
        width: 300px;
      }

      @media(min-height:1230px) {
        bottom: 0;
        right: 0;
        height: 600px;
        width: 550px;
      }
    }

    @include onDesctop {
      height: 540px;
      width: 500px;
      right: 0;
      left: 48%;
      transform: translateX(40px);

      @media(min-height: 800px) {
        height: 697px;
        width: 624px;
      }
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
