@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.visit {
  margin-bottom: 90px;
  background-color: $colorBackground;

  @include onTablet {
    margin-bottom: 125px;
  }

  @include onDesctop {
    margin-bottom: 110px;
  }

  &__container {

    @include onDesctop {
      position: relative;
    }
  }

  &__content {
    padding: 50px 0;
    color: $colorWhite;

    @include onTablet {
      padding: 80px 0;
    }

    @include onDesctop {
      margin-left: 500px;
      padding: 205px 0;
    }
  }

  &__title {
    margin: 0 0 20px;
    font-family: $titleFont;
    font-weight: 700;
    font-size: 38px;
    line-height: 100%;

    @include onTablet {
      margin: 0 0 30px;
      font-size: 64px;
    }

    @include onDesctop {
      font-size: 80px;
    }
  }

  &__text {
    margin: 0 0 40px;

    @include onTablet {
      margin: 0 0 60px;
      max-width: 450px;
    }

    @include onDesctop {
      margin: 0 0 50px;
      max-width: 470px;
    }
  }

  &__button {
    
    @extend %button;

    @include onTablet {
      width: 48%;
    }

    @include onDesctop {
      width: 270px;
    }
  }

  &__background {
    display: none;

    @include onDesctop {
      display: block;
      height: 780px;
      width: 576px;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}
