@import "../utils/mixins";
@import "../utils/vars";

.link {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  font-weight: 600;
  font-size: 16px;
  line-height: 100%;
  cursor: pointer;
  text-decoration: none;
  background-color: #fff;
  color: $colorRed;
  border: 1px solid $colorRed;
  transition: all 0.3s;

  &::after {
    position: absolute;
    top: 50%;
    transform: translate(80px, -50%);

    content: "";
    display: block;
    width: 7px;
    height: 12px;
    background-image: url("../images/icons/arrow-link.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: all 0.3s;
  }

  @include onTablet {
    width: 200px;
    padding-right: 20px;
  }

  @include onDesctop {
    width: 270px;
  }

  &:disabled {
    border: 1px solid #dd836f;
    color: #dd836f;

    &::after {
      background-image: url("../images/icons/arrow-disabled.svg");
    }
  }

  &:hover {
    border: 1px solid #ae3519;
    color: #ae3519;

    &::after {
      background-image: url("../images/icons/arrow-hover.svg");
    }
  }

  &:active {
    border: 1px solid #e9350d;
    color: #e9350d;

    &::after {
      background-image: url("../images/icons/arrow-active.svg");
    }
  }

  &__arrow {
    position: absolute;
    top: 51%;
    transform: translate(70px, -65%);

    display: block;
    margin-left: 13px;
    width: 13px;
    height: 3px;
    background-image: url("../images/icons/arrow-span.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &:disabled &__arrow {
    background-image: url("../images/icons/arrow-span-dis.svg");
  }

  &:hover &__arrow {
    background-image: url("../images/icons/arrow-span-hover.svg");
  }

  &:active &__arrow {
    background-image: url("../images/icons/arrow-span-active.svg");
  }
}
