@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.news {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 160px;
  }

  @include onDesctop {
    margin-bottom: 200px;
  }

  &__content {
    position: relative;
  }

  &__title {
    margin: 0 0 60px;

    @extend %title;

    @include onTablet {
      margin: 0 0 75px;
    }

    @include onDesctop {
      margin: 0 0 55px;
    }
  }

  &__items {

    @include onTablet {
      display: flex;
      flex-wrap: wrap;
      column-gap: 30px;
    }
  }

  &__item {
    margin-bottom: 50px;

    &:nth-child(2) {
      margin-bottom: 30px;
    }

    @include onTablet {
      flex: 1 1 47%;
      margin-bottom: 0;

      &:nth-child(2) {
        margin-bottom: 0;
      }

    }
  }

  &__image {
    margin-bottom: 20px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    min-height: 280px;
    max-height: 500px;
    overflow: hidden;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }

    @include onTablet {
      margin-bottom: 30px;
      min-height: 280px;
      max-height: 370px;
    }

    @include onDesctop {
      margin-bottom: 20px;
    }

    &::after {
      content: "";
      display: block;
      margin-bottom: 100%;
    }
  }

  &__img {
    @extend %img;
  }

  &__date {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: $colorTitle;
  }

  &__info-title {
    margin: 0 0 20px;
    font-family: $titleFont;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    color: $colorTitle;

    @include onTablet {
      margin: 0 0 18px;
    }
  }

  &__info-text {
    margin: 0;
    color: #504f5e;
  }

  &__link-wrapper {

    @include onTablet {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}
