@import "../utils/mixins";
@import "../utils/vars";
@import "../utils/extends";

.exhibitions {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 164px;
  }

  @include onDesctop {
    margin-bottom: 200px;
  }

  &__content {
    position: relative;
  }

  &__title {
    margin: 0 0 60px;

    @extend %title;

    @include onTablet {
      margin: 0 0 75px;
      width: 200px;

      @media(min-width: 730px) {
        width: 420px;
      }
    }

    @include onDesctop {
      margin: 0 0 71px;
      width: 570px;
    }
  }

  &__items {

    @include onTablet {
      display: flex;
      flex-wrap: wrap;
      column-gap: 30px;
    }
  }

  &__item {
    margin-bottom: 60px;

    &:nth-child(2) {
      margin-bottom: 30px;

      @include onTablet {
        margin-bottom: 0;
      }
    }

    @include onTablet {
      flex: 1 1 47%;
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-bottom: 0;
    }
  }

  &__image {
    margin-bottom: 20px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    min-height: 356px;
    max-height: 500px;
    overflow: hidden;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }

    @include onTablet {
      margin-bottom: 30px;
      min-height: 420px;
      max-height: 484px;
    }

    @include onDesctop {
      margin-bottom: 20px;
      height: 484px;
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  }

  &__img {
    @extend %img;
  }

  &__link-wrapper {

    @include onTablet {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}
