@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.footer {
  background-color: $colorBackground;
  color: $colorWhite;

  &__content {
    padding: 34px 0 30px;

    @include onTablet {
      padding: 50px 0 30px;
    }
  }

  &__info {

    @include onTablet {
      margin-bottom: 60px;
      display: flex;
      column-gap: 40px;
      justify-content: space-between;
    }

    @include onDesctop {
      margin-bottom: 70px;
    }
  }

  &__contacts {
    margin-bottom: 50px;

    @include onTablet {
      margin-bottom: 0;
      flex: 1 1 42%;
    }

    @media(min-width: 730px) {
      flex: 1 1 33.33%;
    }
  }

  &__title {
    margin: 0 0 10px;
    font-family: $titleFont;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;

    @include onTablet {
      font-style: 20px;
      margin: 0 0 33.33px;
    }
  }

  &__contacts-link {
    display: inline-block;
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s;

    &--address {
      display: inline;
    }

    &:nth-child(4) {
      margin-bottom: 30px;

      @include onTablet {
        margin-bottom: 50px;
      }
    }

    &:hover {
      border-bottom: 1px solid $colorWhite;
    }
  }

  &__phone-link {
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s;

    &:hover {
      border-bottom: 1px solid $colorWhite;
    }
  }

  &__icons {
    display: flex;
    column-gap: 25px;
    align-items: center;
  }

  &__schedule {
    margin-bottom: 50px;

    @include onTablet {
      margin-bottom: 0;
      flex: 1 1 33.33%;
    }
  }

  &__days {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__links {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 0;
      flex: 1 1 30%;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 5px;

    @include onTablet {
      margin-bottom: 10px;
    }
  }

  &__link {
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s;

    &:hover {
      border-bottom: 1px solid $colorWhite;
    }
  }

  &__date {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__copyright {
    display: flex;
    column-gap: 22px;
    align-items: center;

    @include onDesctop {
      column-gap: 102px;
    }
  }

  &__copyright-item {
    display: block;
    font-size: 14px;
    line-height: 140%;
  }

  &__link-arrow {
    position: relative;
    height: 30px;
    width: 30px;
    border: 2px solid transparent;
    border-radius: 50%;
    transition: border 0.3s;

    &:hover {
      border: 2px solid $colorWhite;
    }
  }

  &__arrow1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -130%);
    height: 8px;
    width: 18px;
    background-image: url("../images/icons/footer-arrow.svg");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

  }

  &__arrow2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 20px;
    width: 2px;
    background-image: url("../images/icons/footer2.svg");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
}
