@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.subscrible {
  margin-bottom: 80px;

  @include onTablet {
    margin-bottom: 128px;
  }

  @include onDesctop {
    margin-bottom: 200px;
  }

  &__title-wrapper {
    @include onDesctop {
      margin-bottom: 50px;
      display: flex;
      column-gap: 30px;
    }
  }

  &__title {
    margin: 0 0 20px;

    @extend %title;

    @include onDesctop {
      margin: 0;
      box-sizing: border-box;
      max-width: 570px;
      flex: 1 1 48%;
    }
  }

  &__info {
    margin: 0 0 50px;

    @include onTablet {
      max-width: 570px;
    }

    @include onDesctop {
      margin-bottom: 0;
      flex: 1 1 48%;
    }
  }

  &__field {
    display: block;
    box-sizing: border-box;
    padding: 0 0 3px;
    margin: 0 0 30px;
    width: 100%;
    border: 0;
    border-bottom: 1px solid $colorTitle;
    outline: none;
    font-family: inherit;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: #1c1b29;

    transition: all 0.3s;

    &:hover {
      border-bottom: 1px solid $colorRed;
    }

    &:focus {
      border-bottom: 1px solid $colorRed;
      color: $colorRed;
    }

    &::placeholder {
      font-family: "Raleway", sans-serif;
      font-weight: 400;
      font-size: 16px;
      color: $colorTitle;
      transition: all 0.3s;
    }

    &:hover::placeholder {
      color: $colorRed;
    }

    @include onTablet {
      width: 58%;
    }

    @include onDesctop {
      margin-bottom: 40px;
      width: 470px;
    }
  }

  &__btn {
    @extend %button;

    width: 100%;
    box-sizing: border-box;
    border: 1px solid transparent;

    @include onTablet {
      width: 48%;
    }

    @include onDesctop {
      width: 270px;
    }
  }
}
