@import "../utils/mixins";
@import "../utils/extends";
@import "../utils/vars";

.info {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__time {
    margin-bottom: 10px;
    color: $colorRed;
  }

  &__title {
    margin: 0 0 20px;
    font-family: $titleFont;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    color: $colorTitle;
  }

  &__text {
    margin: 0 0 30px;
    color: #504f5e;

    &--events {
      @include onTablet {
        max-width: 570px;
      }
    }

    @include onTablet {
      margin: 0 0 40px;
      flex: 1 1 auto;

      @media(min-width: 950px) {
        flex: 0 0 auto;
      }
    }

    @include onDesctop {
      margin: 0 0 50px;
    }
  }

  &__link {
    @extend %button;

    @include onDesctop {
      width: 270px;
    }
  }
}
