@import "../utils/mixins";
@import "../utils/vars";

.icons {
  display: flex;
  align-items: center;
  column-gap: 43px;

  @include onTablet {
    margin-right: 10px;
    flex-direction: row-reverse;
    column-gap: 40px;
  }

  &__icon-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 20px;
    text-decoration: none;
  }

  &__icon-lang {
    position: relative;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    text-decoration: none;
    color: $colorWhite;
    transition: color 0.3s;

    &:hover {
      color: $colorRed;
    }

    &::after {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(12px, -50%);

      @include onTablet {
        transform: translate(10px, -50%);
      }

      content: "";
      display: block;
      width: 8px;
      height: 5px;
      background-image: url("../images/icons/arrow-lang.svg");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__menu-opener {
    position: relative;
    display: block;
    width: 25px;
    height: 2px;
    background-color: $colorWhite;

    &::before,
    &::after {
      position: absolute;
      content: "";
      display: block;
      width: 25px;
      height: 2px;
      background-color: $colorWhite;
    }

    &::before {
      transform: translateY(-7px);
    }

    &::after {
      transform: translateY(7px);
    }
  }

  &__menu-close {
    position: relative;
    display: block;
    width: 25px;
    height: 25px;
    transform: translate(-44%, 30%) rotate(45deg);

    &::before,
    &::after {
      position: absolute;
      content: "";
      display: block;
      width: 28px;
      height: 2px;
      background-color: $colorWhite;
    }

    &::before {
      transform: rotate(90deg);
    }

    // &::after {
    // }

  }
}
