@import "../utils/extends";
@import "../utils/mixins";
@import "../utils/vars";

.events {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 100px;
  }

  @include onDesctop {
    margin-bottom: 110px;
  }

  &__content {
    position: relative;
  }

  &__title {
    margin: 0 0 60px;

    @extend %title;

    @include onTablet {
      margin: 0 0 75px;
      width: 200px;

      @media(min-width: 730px) {
        width: 420px;
      }
    }

    @include onDesctop {
      margin: 0 0 70px;
      width: 570px;
    }
  }

  &__item {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 70px;
      display: flex;
      column-gap: 30px;
    }

    &:nth-child(2) {
      margin-bottom: 30px;

      @include onTablet {
        margin-bottom: 0;
      }
    }
  }

  &__image {
    margin-bottom: 20px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    min-height: 358px;
    max-height: 500px;
    overflow: hidden;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onTablet {
      flex: 1 1 50%;
      min-height: 422px;
      max-height: 500px;
    }

    @include onDesctop {
      flex: 1 1 31%;
      height: 475px;
    }
  }

  &__img {
    @extend %img;
  }

  &__info {

    @include onTablet {
      flex: 1 1 50%;
    }

    @include onDesctop {
      flex: 1 1 69%;
    }
  }

  &__link-wrapper {

    @include onTablet {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}
